<template>
  <div>
    <Nav></Nav>
    <div class="img_bg">
      <div class="block">
        <div class="login_block">
          <div class="title">
            <div @click="show=1;barAnimate(0,1)">注 册</div>
            <div @click="show=2;barAnimate(50,2)">登 录</div>
            <div id="bar" class="bar"></div>
          </div>
          <div class="from">
            <!-- 手机号登录开始 -->
            <div v-show="show == 1">
              <div class="input_phone">
                <input
                  maxlength="11"
                  type="text"
                  v-model="form.phone"
                  max="11"
                  placeholder="请输入手机号"
                  @keyup="phone"
                />
                <transition
                  enter-active-class="animated fadeInDown"
                  leave-active-class="animated fadeOutDown"
                >
                  <span class="tips" v-show="!rules.phone.verif">{{rules.phone.msg}}</span>
                </transition>
              </div>
              <br />
              <div class="input_pass">
                <input
                  type="password"
                  v-model="form.password"
                  placeholder="请输入密码"
                  @keyup="password"
                />
                <transition
                  enter-active-class="animated fadeInDown"
                  leave-active-class="animated fadeOutDown"
                >
                  <span class="tips" v-show="!rules.password.verif">{{rules.password.msg}}</span>
                </transition>
              </div>
              <br />
              <div class="input_pass">
                <input
                  type="password"
                  v-model="form.passwordB"
                  placeholder="确认密码"
                  @keyup="passwordB"
                />
                <transition
                  enter-active-class="animated fadeInDown"
                  leave-active-class="animated fadeOutDown"
                >
                  <span class="tips" v-show="!rules.passwordB.verif">{{rules.passwordB.msg}}</span>
                </transition>
              </div>
              <div class="input_wrap">
                <input
                  class="input_mt20"
                  type="text"
                  v-model="form.code"
                  placeholder="请输入验证码"
                  maxlength="6"
                  @keyup="code"
                />
                <transition
                  enter-active-class="animated fadeInDown"
                  leave-active-class="animated fadeOutDown"
                >
                  <span v-show="!rules.code.verif" class="tips">{{rules.code.msg}}</span>
                </transition>
                <div class="code" @click="getRegisterCode" v-if="countDownNub == 60">获取验证码</div>
                <div class="code" style="background: #8892a0;" v-else>{{`${countDownNub}后重新获取`}}</div>
              </div>
              <div class="btn" @click="btn">注册登录</div>
              <div class="btn_tips">
                <span>填写手机号/密码,获取验证码完成注册</span>
              </div>
            </div>
            <!-- 手机号登录结束 -->

            <!-- 账号密码登录开始 -->
            <div v-show="show==2">
              <div class="input_phone">
                <input
                  type="text"
                  maxlength="11"
                  max="11"
                  v-model="form2.phone"
                  placeholder="请输入手机号"
                  @keyup="phone2"
                />
                <transition
                  enter-active-class="animated fadeInDown"
                  leave-active-class="animated fadeOutDown"
                >
                  <span class="tips" v-show="!rules2.phone.verif">{{rules2.phone.msg}}</span>
                </transition>
              </div>
              <!-- 密码输入框 -->
              <div class="input_wrap" v-if="!qrcode">
                <input
                  class="input_mt20"
                  type="password"
                  v-model="form2.password"
                  placeholder="请输入密码"
                  @keyup="password2"
                />
                <transition
                  enter-active-class="animated fadeInDown"
                  leave-active-class="animated fadeOutDown"
                >
                  <span class="tips" v-show="!rules2.password.verif">{{rules2.password.msg}}</span>
                </transition>
              </div>
              <!-- 验证码输入框开始 -->
              <div class="input_wrap" v-else>
                <input
                  class="input_mt20"
                  type="text"
                  v-model="form2.code"
                  placeholder="请输入验证码"
                  maxlength="6"
                  @keyup="code2"
                />
                <transition
                  enter-active-class="animated fadeInDown"
                  leave-active-class="animated fadeOutDown"
                >
                  <span v-show="!rules2.code.verif" class="tips">{{rules2.code.msg}}</span>
                </transition>
                <div class="code" @click="getVerifyCode" v-if="countDownNub == 60">获取验证码</div>
                <div class="code" style="background: #8892a0;" v-else>{{`${countDownNub}后重新获取`}}</div>
              </div>
              <!-- 验证码输入框结束 -->
              <div class="btn" @click="btn">{{qrcode == false ? '登 录' : '验证码登录'}}</div>
              <div class="btn_tips">
                <span
                  style="cursor: pointer;"
                  @click="qrcode = !qrcode"
                >{{qrcode == false ? '使用验证码登录' : '使用账号密码登录'}} |</span>
                <a :href="adminUrl" style="color:#a3a3a3;">管理后台</a>
              </div>
            </div>
            <!-- 账号密码登录结束 -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Nav from "../../components/base/nav/menus";
import Velocity from "velocity-animate";
import { getRegisterCode, getVerifyCode, loginRegister } from "../../api/user";
import { messages, adminUrl } from "../../utils";
import { mapState, mapActions } from "vuex";
export default {
  components: {
    Nav
  },
  computed: {
    ...mapState("app", ["token"])
  },
  data() {
    return {
      adminUrl,
      countDownNub: 60,
      show: 2, //1 注册 2账号密码登录
      qrcode: false, //是否使用验证码
      //手机登录信息
      form: {
        phone: "",
        password: "",
        passwordB: "",
        code: ""
      },
      // 账号密码登录信息
      form2: {
        phone: "",
        password: "",
        code: ""
      },
      // 手机登录验证
      rules: {
        phone: {
          msg: "手机号必须11位数",
          verif: true
        },
        password: {
          msg: "请输入字母与数字6位以上密码",
          verif: true
        },
        passwordB: {
          msg: "两次密码不相同",
          verif: true
        },
        code: {
          msg: "验证码为6位数字",
          verif: true
        }
      },
      //账号密码登录验证
      rules2: {
        phone: {
          msg: "手机号必须11位数",
          verif: true
        },
        password: {
          msg: "密码不能为空",
          verif: true
        },
        code: {
          msg: "验证码为6位数字",
          verif: true
        }
      }
    };
  },
  methods: {
    ...mapActions("app", ["LoginByUsername", "LoginCode", "LoginRegister"]),
    //表单验证
    phone() {
      return (this.rules.phone.verif = this.form.phone.length == 11);
    },
    password() {
      let reg = new RegExp(/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/);
      return (this.rules.password.verif = reg.test(this.form.password));
    },
    passwordB() {
      return (this.rules.passwordB.verif =
        this.form.password == this.form.passwordB);
    },
    code() {
      return (this.rules.code.verif = this.form.code.length == 6);
    },
    phone2() {
      return (this.rules2.phone.verif = this.form2.phone.length == 11);
    },
    password2() {
      return (this.rules2.password.verif = this.form2.password != "");
    },
    code2() {
      return (this.rules2.code.verif = this.form2.code.length == 6);
    },
    /**
     * @function 注册+验证码
     * @param { phone 手机号 }
     */
    getRegisterCode() {
      if (this.phone()) {
        getRegisterCode({ phone: this.form.phone }).then(res => {
          if (res.data.status == "SUCCESS") {
            messages.success(`验证码获取成功,验证码5分钟内有效`);
            this.countDown();
          }
        });
      }
    },
    /**
     * @function 手机验证码登录+验证码
     * @param { phone 手机号 }
     */
    getVerifyCode() {
      if (this.phone2()) {
        getVerifyCode({ phone: this.form2.phone }).then(res => {
          if (res.data.status == "SUCCESS") {
            messages.success(`验证码获取成功,验证码5分钟内有效`);
            this.countDown();
          }
        });
      }
    },
    /**
     * @function 登录标题底下滑动条
     * @param value 滑动距离值
     * @param show 显示手机登录还是账号密码登录
     */
    barAnimate(value, show) {
      Velocity(document.getElementById("bar"), "stop");
      Velocity(
        document.getElementById("bar"),
        {
          left: `${value}%`
        },
        {
          begin() {
            this.show = show;
          },
          easing: "easeInOutCirc",
          duration: 400
        }
      );
    },
    /**
     * @function 注册/账号密码登录/验证码登录
     */
    btn() {
      //注册
      if (this.show == 1) {
        if (this.phone() & this.password() & this.passwordB() & this.code()) {
          this.LoginRegister({
            phone: this.form.phone,
            password: this.form.passwordB,
            code: this.form.code
          }).then(res => {
            messages.success(`注册成功,欢迎使用华联投`);
            // 路由跳转
            this.$router.push({
              path:
                this.$route.query.redirect == undefined
                  ? "/index"
                  : this.$route.query.redirect
            });
          });
        }
      } else {
        //登录
        if (this.qrcode) {
          //手机验证码登录
          if (this.phone2() & this.code2()) {
            this.LoginCode({
              phone: this.form2.phone,
              code: this.form2.code
            })
              .then(res => {
                messages.success(`手机验证码成功,欢迎使用华联投`);
                // 路由跳转
                this.$router.push({
                  path:
                    this.$route.query.redirect == undefined
                      ? "/index"
                      : this.$route.query.redirect
                });
              })
              .catch(err => {});
          }
        } else {
          //账号密码登录
          if (this.phone2() & this.password2()) {
            this.LoginByUsername({
              phone: this.form2.phone,
              password: this.form2.password
            })
              .then(res => {
                messages.success(`账号登录成功,欢迎使用华联投`);
                // 路由跳转
                this.$router.push({
                  path:
                    this.$route.query.redirect == undefined
                      ? "/index"
                      : this.$route.query.redirect
                });
              })
              .catch(err => {});
          }
        }
      }
    },
    // 倒计时
    countDown() {
      this.countDownNub = this.countDownNub - 1;
      if (this.countDownNub == 0) {
        this.countDownNub = 60;
        return;
      }
      let _this = this;
      setTimeout(function() {
        _this.countDown();
      }, 1000);
    },
    // 键盘按下事件 判断是否是回车
    keyDown(e) {
      if (e.keyCode == 13) {
        this.btn();
      }
    }
  },
  mounted() {
    // 组件加载完成加入监听
    document.addEventListener("keydown", this.keyDown);
  },
  beforeDestroy() {
    // 组件销毁钱接触键盘监听
    document.removeEventListener("keydown", this.keyDown);
  }
};
</script>
